import { useEffect, useState } from "react";

import BranchItem from "./branchItem";

import searchIcon from "../../../assets/icons/search.png";
import "./styles.scss";
function Pickup({ history, match }) {
  const [search, setSearch] = useState("");
  const [branches, setbranches] = useState([]);

  const onSearch = (v) => {
    setSearch(v);
  };

  const selectBranch = (selectedBranch, name) => {
    const { path } = match;
    // let brPath = name.trim().toLowerCase()
    // brPath.replaceAll(" ","-")

    history.push(`${path}/${name}`);
  };
  return (
    <>
      <section className="search">
        <div className="search-inner">
          <img src={searchIcon} alt="search icon" className="icn" />
          <input
            style={{ outline: "none" }}
            onChange={(e) => onSearch(e.target.value)}
            type="text"
            name="search-bar"
            id="search-bar"
            placeholder="Search for an outlet"
          />
        </div>
      </section>
      <div className="branches-container">
        {branches
          .filter((a) =>
            `${a.brands} : ${a.branchName}`
              .toLowerCase()
              .includes(search?.toLowerCase())
          )
          .map((a, i) => (
            <div key={i} onClick={() => selectBranch(a, a.id)}>
              <BranchItem branch={a} />
            </div>
          ))}
      </div>
    </>
  );
}

export default Pickup;
